n// @import \"../../uikit/elements/elements-page/elements-page.scss\";\n// @import \"../../uikit/colors-and-type/colors-and-type.scss\";\n// @import \"../../uikit/cards/cards-page/cards-page.scss\";\n\n// ================Fonts=================\n%type-h1 {\n  font-weight: 700;\n  font-size: 24px;\n  line-height: 1.2;\n}\n\n%type-b1 {\n  font-weight: 700;\n  font-size: 12px;\n  line-height: 1.2;\n  text-transform: uppercase;\n}\n%type-h6 {\n  font-weight: 700;\n  font-size: 14px;\n  line-height: 1.2;\n}\n%type-body {\n  font-weight: 400;\n  font-size: 14px;\n  line-height: 1.9;\n}\n%type-m1 {\n  font-weight: 400;\n  font-size: 14px;\n  line-height: 1.3;\n}\n%type-s {\n  font-weight: 400;\n  font-size: 12px;\n  line-height: 1.2;\n}\n%type-s1 {\n  font-weight: 400;\n  font-size: 14px;\n  line-height: 1;\n}\n%type-c {\n  font-weight: 400;\n  font-size: 12px;\n  line-height: 1.2;\n  text-transform: uppercase;\n}\n// ================FontsGroups=================\n%font-header-link {\n  @extend %type-m1;\n  color: $dark-50;\n}\n%font-h1 {\n  @extend %type-h1;\n}\n\n%font-h3 {\n  @extend %type-b1;\n}\n%font-body {\n  @extend %type-body;\n}\n%font-field {\n  @extend %type-body;\n  color: $dark-75;\n}\n%font-checkbox {\n  @extend %type-m1;\n  color: $dark-50;\n}\n%font-rich-checkbox-buttons-title {\n  @extend %type-h6;\n  color: $dark-75;\n}\n%font-rich-checkbox-buttons-subtext {\n  @extend %type-s;\n  color: $dark-50;\n}\n// ================Total=================\n\nhtml {\n  font-size: $mainFontSize;\n  font-family: $mainFontFamily;\n  color: $mainFontColor;\n  background: $bg-color;\n}\n\nbody {\n}\n\n.wrapper {\n  display: flex;\n  flex-direction: column;\n  min-height: 100%;\n  overflow: hidden;\n}\n\n.main {\n  flex: 1 1 auto;\n  margin-top: 70px;\n  @include mb(100px);\n}\n\n.container {\n  @include container();\n  box-sizing: content-box;\n}\n\n.ibg {\n  position: relative;\n  & img {\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n  }\n}\n\n._lock {\n  overflow: hidden;\n}\n\n.card {\n  padding: 40px 30px 30px;\n  background: #ffffff;\n  border: 1px solid rgba(0, 0, 0, 0.12);\n  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);\n  border-radius: 4px;\n}\n","@use \"sass:math\";\n\n@import \"./vars.scss\";\n\n// px to rem\n//font-size: rem(20);\n//in html{font-size: $mainFontSize; media(---){font-size: 15px}; media(---){font-size: 14px}...\n@function rem($pixels, $context: $maxFontSize) {\n  @if (unitless($pixels)) {\n    $pixels: $pixels * 1px;\n  }\n  @if (unitless($context)) {\n    $context: $context * 1px;\n  }\n  @return math.div($pixels, $context) * 1rem;\n  // @return ($pixels * $context) * 1rem;\n}\n\n// @mixin af($pcSize, $mobSize) {\n// \t$addSize: $pcSize - $mobSize;\n// \t$addMobSize: $addSize + $addSize * 0.7;\n// \t@media (max-width: 767px) {\n// \t\tfont-size: calc(#{$mobSize + \"px\"} + #{$addMobSize + \"px\"} * ((100vw-320px) / #{$maxWidth}));\n// \t}\n// \t@media (min-width: 767px) {\n// \t\tfont-size: calc(#{$mobSize + \"px\"} + #{$addSize + \"px\"} * (100vw / #{$maxWidth}));\n// \t}\n// }\n\n$maxWidth: 1220;\n@mixin adaptiv-font($pcSize, $mobSize) {\n  $addSize: $pcSize - $mobSize;\n  $maxWidth: $maxWidth - 320;\n  font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));\n}\n\n@mixin size-obj($width, $height: $width) {\n  width: $width;\n  height: $height;\n}\n\n@mixin placeholder {\n  ::-webkit-input-placeholder {\n    @content;\n  }\n  :-moz-placeholder {\n    @content;\n  }\n  ::-moz-placeholder {\n    @content;\n  }\n  :-ms-input-placeholder {\n    @content;\n  }\n}\n// layout helper\n@mixin mr($margin) {\n  & > * {\n    margin-right: $margin;\n    &:last-child {\n      margin-right: 0;\n    }\n  }\n}\n@mixin mb($margin) {\n  & > * {\n    margin-bottom: $margin;\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}\n\n@mixin pbody($padding-top, $padding-bottom) {\n  & {\n    padding-top: calc($padding-top / $container * 100%);\n    padding-bottom: calc($padding-bottom / $container * 100%);\n  }\n}\n\n// media query\n@mixin breakpoint($br) {\n  @media (max-width: $br) {\n    @content;\n  }\n}\n","@use \"sass:math\";\n\n$columns: 12;\n$atom: math.div(100%, $columns);\n\n$break_xxl: 1400px;\n$break_xl: 1200px;\n$break_lg: 992px;\n$break_md: 768px;\n$break_sm: 576px;\n$break_xs: 425px;\n\n$offset: 30px;\n$offset_xxl: $offset;\n$offset_xl: $offset;\n$offset_lg: $offset;\n$offset_md: $offset;\n$offset_sm: $offset;\n$offset_xs: $offset;\n\n$offset_one_side: ($offset * 0.5);\n$offset_xxl_one_side: ($offset_xxl * 0.5);\n$offset_xl_one_side: ($offset_xl * 0.5);\n$offset_lg_one_side: ($offset_lg * 0.5);\n$offset_md_one_side: ($offset_md * 0.5);\n$offset_sm_one_side: ($offset_sm * 0.5);\n$offset_xs_one_side: ($offset_xs * 0.5);\n\n$fields: 15px;\n$fields_lg: 15px;\n\n@mixin container-full() {\n  padding-left: $fields;\n  padding-right: $fields;\n\n  @include lg-block() {\n    padding-left: $fields_lg;\n    padding-right: $fields_lg;\n  }\n}\n\n@mixin row-offsets() {\n  margin-left: ($offset_one_side * -1);\n  margin-right: ($offset_one_side * -1);\n}\n\n@mixin col-offsets($type) {\n  #{$type}-left: $offset_one_side;\n  #{$type}-right: $offset_one_side;\n}\n\n@mixin container() {\n  max-width: 1160px;\n  margin: 0 auto;\n  @include container-full();\n}\n\n@mixin row-flex() {\n  display: flex;\n  flex-wrap: wrap;\n  @include row-offsets();\n}\n\n@mixin row-float() {\n  @include row-offsets();\n  @include clearfix();\n}\n\n@mixin row-ib() {\n  @include row-offsets();\n}\n\n@mixin col() {\n  box-sizing: border-box;\n  word-wrap: break-word;\n  @include col-offsets(margin);\n}\n\n@mixin col-float() {\n  float: left;\n}\n\n@mixin col-ib() {\n  display: inline-block;\n  vertical-align: top;\n}\n\n@mixin col-padding() {\n  word-wrap: break-word;\n  @include col-offsets(padding);\n}\n\n@mixin size($n) {\n  $val: math.div(100%, $columns) * $n;\n  width: #{calc(#{$val} - #{$offset})};\n}\n\n@mixin size-xxl($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include xxl(width, #{calc(#{$val} - #{$offset_xxl})});\n}\n\n@mixin size-xl($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include xl(width, #{calc(#{$val} - #{$offset_xl})});\n}\n\n@mixin size-lg($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include lg(width, #{calc(#{$val} - #{$offset_lg})});\n}\n\n@mixin size-md($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include md(width, #{calc(#{$val} - #{$offset_md})});\n}\n\n@mixin size-sm($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include sm(width, #{calc(#{$val} - #{$offset_sm})});\n}\n\n@mixin size-xs($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include xs(width, #{calc(#{$val} - #{$offset_xs})});\n}\n\n@mixin shift($n) {\n  $val: math.div(100%, $columns) * $n;\n  margin-left: #{calc(#{$val} + #{$offset_one_side})};\n  margin-right: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-left($n) {\n  $val: math.div(100%, $columns) * $n;\n  margin-left: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-right($n) {\n  $val: math.div(100%, $columns) * $n;\n  margin-right: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-padding($n) {\n  $val: math.div(100%, $columns) * $n;\n  padding-left: #{calc(#{$val} + #{$offset_one_side})};\n  padding-right: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-padding-left($n) {\n  $val: math.div(100%, $columns) * $n;\n  padding-left: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-padding-right($n) {\n  $val: math.div(100%, $columns) * $n;\n  padding-right: #{calc(#{$val} + #{$offset_one_side})};\n}\n\n@mixin shift-xxl($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include xxl(margin-left, #{calc(#{$val} + #{$offset_xxl_one_side})});\n  @include xxl(margin-right, #{calc(#{$val} + #{$offset_xxl_one_side})});\n}\n\n@mixin shift-xxl-left($n) {\n  $val: math.div(100%, $columns) * $n;\n  @include xxl(margin-left, #{calc(#{$val} + #{$offset_xxl_one_side})});\n}\n\n@mi